import { filter } from 'lodash';
import { Icon } from '@iconify/react';
import { sentenceCase } from 'change-case';
import { useState, useEffect } from 'react';
import plusFill from '@iconify/icons-eva/plus-fill';
import { Link as RouterLink } from 'react-router-dom';
// material
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Stack,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination
} from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getCompagnies } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import {
  CompagniesListHead,
  CompagniesListToolbar,
  CompagniesMoreMenu
} from '../../components/_dashboard/compagnies/list';
import useAuth from '../../hooks/useAuth';
import { handlePermission } from '../../utils/handlePermission';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'Nom complet', alignRight: false },
  { id: 'ref_code', label: 'Ref_code', alignRight: false },
  { id: 'phone', label: 'Téléphone', alignRight: false },
  { id: 'country', label: 'Pays', alignRight: false },
  { id: 'city', label: 'Ville', alignRight: false },
  { id: 'addresse_complet', label: 'Adresse complète', alignRight: false },
  { id: 'block', label: 'Block', alignRight: false },
  { id: '' }
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array?.map((el, index) => [el, index]);
  stabilizedThis?.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_supervisor) => _supervisor?.first_name.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis?.map((el) => el[0]);
}

export default function AppPromote() {
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const dispatch = useDispatch();
  const { compagnies } = useSelector((state) => state.user);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('first_name');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(25);
  const [loading, setLoading] = useState(false);

  const handleGo = async () => {
    setLoading(true);
    await dispatch(getCompagnies());
    setLoading(false);
  };

  useEffect(() => {
    handleGo();
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = compagnies?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const handleDeleteSupervisor = (supervisorId) => {
    // dispatch(deleteSupervisor(supervisorId));
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - compagnies.length) : 0;

  const filteredCompagnies = applySortFilter(compagnies, getComparator(order, orderBy), filterName);

  const isCompagniesNotFound = filteredCompagnies?.length === 0;

  return (
    <Page title="Compagnies: List | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Liste des Compagnies"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Compagnies', href: PATH_DASHBOARD.company?.root },
            { name: 'Listes' }
          ]}
          action={
            handlePermission(user?.perms, 'add_company') && (
              <Button
                variant="contained"
                component={RouterLink}
                to={PATH_DASHBOARD.company?.newCompany}
                startIcon={<Icon icon={plusFill} />}
              >
                Ajouter Compagnie
              </Button>
            )
          }
        />

        <Card>
          <CompagniesListToolbar
            handleGo={handleGo}
            loading={loading}
            filterName={filterName}
            onFilterName={handleFilterByName}
          />
          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <CompagniesListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={compagnies?.length}
                  numSelected={selected?.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {(rowsPerPage > 0
                    ? filteredCompagnies?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    : filteredCompagnies
                  )?.map((ite) => {
                    const isItemSelected = selected.indexOf(ite?.id) !== -1;

                    return (
                      <TableRow
                        hover
                        key={ite?.id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell component="th" scope="row" padding="none">
                          <Stack direction="row" alignItems="center" spacing={2}>
                            <Avatar alt={ite?.companyname} src={ite?.user_photo} />
                            <Typography variant="subtitle2" noWrap>
                              {ite?.companyname}
                            </Typography>
                          </Stack>
                        </TableCell>
                        <TableCell align="left">{ite?.ref_code}</TableCell>
                        <TableCell align="left">{ite?.phone}</TableCell>
                        <TableCell align="left">{ite?.country_name}</TableCell>
                        <TableCell align="left">{ite?.city_name}</TableCell>
                        <TableCell align="left">{ite?.addresse_complet}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(ite?.block && 'error') || 'success'}
                          >
                            {ite?.block ? 'Oui' : 'Non'}
                          </Label>
                        </TableCell>

                        <TableCell align="right">
                          {/* <CompagniesMoreMenu block={ite?.block} supervisorId={ite?.id} /> */}
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isCompagniesNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            labelRowsPerPage="Lignes par page"
            rowsPerPageOptions={[25, 50, 75, 100, { label: 'Tout', value: -1 }]}
            component="div"
            count={compagnies?.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
