import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { paramCase } from 'change-case';
import { useRef, useState } from 'react';
import editFill from '@iconify/icons-eva/edit-fill';
import unlockFill from '@iconify/icons-eva/unlock-fill';
import lockFill from '@iconify/icons-eva/lock-fill';
import { Link as RouterLink } from 'react-router-dom';
import trash2Outline from '@iconify/icons-eva/trash-2-outline';
import moreVerticalFill from '@iconify/icons-eva/more-vertical-fill';
import { useSnackbar } from 'notistack';
// material
import { Menu, MenuItem, IconButton, ListItemIcon, ListItemText } from '@mui/material';
import Request from '../../../../utils/Request';
import { useDispatch, useSelector } from '../../../../redux/store';
import { getCompagnies } from '../../../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../../../routes/paths';
import { PERMISSIONS } from '../../../../guards/Permissions';
import useAuth from '../../../../hooks/useAuth';
import { handlePermission } from '../../../../utils/handlePermission';

// ----------------------------------------------------------------------

CompagnieMoreMenu.propTypes = {
  companyId: PropTypes.number,
  block: PropTypes.bool
};

export default function CompagnieMoreMenu({ block, companyId }) {
  const ref = useRef(null);
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();

  const handleBlock = async () => {
    setIsOpen(false);
    const values = {
      companyId,
      block
    };
    await Request.handleModifyCompagnie(values);
    dispatch(getCompagnies());
    enqueueSnackbar(!block ? 'Block with success' : 'unblock with success', { variant: 'success' });
  };

  const handleDelete = async () => {
    setIsOpen(false);
    const values = {
      companyId
    };
    await Request.handleDeleteCompagnie(values);
    dispatch(getCompagnies());
    enqueueSnackbar('delete with success', { variant: 'success' });
  };

  return (
    <>
      <IconButton ref={ref} onClick={() => setIsOpen(true)}>
        <Icon icon={moreVerticalFill} width={20} height={20} />
      </IconButton>

      <Menu
        open={isOpen}
        anchorEl={ref.current}
        onClose={() => setIsOpen(false)}
        PaperProps={{
          sx: { width: 200, maxWidth: '100%' }
        }}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        {handlePermission(user?.perms, 'delete_company') && (
          <MenuItem
            onClick={() => {
              const confirmation = window.confirm('Êtes-vous sûr de bien vouloir supprimer?');

              if (confirmation) {
                // Perform the deletion logic here
                // You can call your API or update state as needed.
                handleDelete();
              }
            }}
            sx={{ color: 'red' }}
          >
            <ListItemIcon>
              <Icon icon={trash2Outline} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary="Delete" primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )}
        {/* {handlePermission(user?.perms, PERMISSIONS.BLOCK_SUP) && (
          <MenuItem onClick={handleBlock} sx={{ color: 'text.secondary' }}>
            <ListItemIcon>
              <Icon icon={!block ? lockFill : unlockFill} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary={!block ? 'Block' : 'Unblock'} primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )} */}
        {handlePermission(user?.perms, 'change_company') && (
          <MenuItem
            component={RouterLink}
            to={`${PATH_DASHBOARD.company?.root}/${companyId}/edit`}
            sx={{ color: 'text.secondary' }}
          >
            <ListItemIcon>
              <Icon icon={editFill} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary="Edit" primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )}
      </Menu>
    </>
  );
}
